import { RPTooltip } from "@rp/react/web/common/base-components"
import { ShareIcon } from "@rp/react/web/common/base-components"
import { IconButton } from "@rp/react/web/mui/core"

type Props = {
  onClick: () => void
  isPublic: boolean
}

const ShareSearchButton = ({ onClick, isPublic }: Props) => {
  return (
    <RPTooltip title="Share">
      <IconButton
        onClick={e => {
          e.stopPropagation()
          onClick()
        }}
      >
        <ShareIcon shared={isPublic} />
      </IconButton>
    </RPTooltip>
  )
}

export { ShareSearchButton }
